// Global Variables
:root {
    --primary: #2178FF;
    --nero: #171717;
    --header-height: 98px;
    --header-mobile-height: 76px;

}




  
// Reset
html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
*, *:before, *:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

body {
    color: var(--nero);
    font-family: 'AvenirMedium', sans-serif;
    font-size: 16px;
  
    @media (min-width: 1200px) {
      font-size: 18px;
    }
}


// Layout
.container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
  
    @media (min-width: 576px) {
      max-width:540px
    }
    @media (min-width: 768px) {
      max-width:720px
    }
    
    @media (min-width: 992px) {
      max-width:960px
    }
    
    @media (min-width: 1200px) {
      max-width:1140px
    }
}





//  Buttons
.btn {
    border: 1px solid transparent;
    cursor: pointer;
}
.btn__link {
    background-color: transparent;
    &:focus {
      outline: 0;
    }
}

.group__btn {
  &> * {
    margin: 0 6px;
  }
}

.btn--primary {
  font-size: 16px;
  background-color: #2178FF;
  color: white;
  box-shadow: 0 5px 14px #2178ff;
  border-radius: 22.5px;
  border: none;
  height: 45px;

  &.see-more__btn {
    width: 150px;
  }
}


.googleplay__img {
  width: 141px;
  height: 42px;
  object-fit: contain;

  @media (min-width: 992px) {
    width: 185px;
    height: 55px;
  }
}
  
.appstore__img {
  width: 126px;
  height: 42px;
  object-fit: contain;

  @media (min-width: 992px) {
    width: 185px;
    height: 55px;
  }
}
  
  
  
// Lists
.list {
  list-style-type: none;
  padding-left: 10px;
  margin: 0 0 30px;
}
.list__item {
  margin: 0;
  margin-bottom: 1em;
  padding-left: 20px;
  position: relative;
  
  &:after {
      content: '';
      height: .4em;
      width: .4em;
      background: var(--nero);
      display: block;
      position: absolute;
      transform: rotate(45deg);
      top: 6px;
      left: 0;
  }
  }




  
  
// Header
.slip__header {
    background-color: white;
    padding: 1rem 0;

    @media (min-width: 992px) {
      padding: 0;
    }
}
.nav__link-logo {
  margin-right: 70px;
  color: var(--primary);
  width: 60px;
  height: 44px;

  @media (min-width: 992px) {
    width: 78px;
    height: 58px;
  }
}  
.nav__btn {
    display: block;

    @media (min-width: 992px) {
      display: none;
    }
}
.slip__navbar {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: height 1s ease-in-out;


    @media (min-width:1200px) {
        height: var(--header-height);
    }

    &.collapsed {
      @media (max-width: 992px) {
        align-items: flex-start;
        flex-wrap: wrap;
        height: 100vh;
        z-index: 1;
        
        .slip__nav {
            visibility: visible;
            width: 100%;
            top: 90px;
            left: 0;
            right: 0;
            display: flex;
            flex-direction: column;
            transform: translateY(0);
            z-index: 0;
        }
        .nav__btn {
          margin-top: 10px;
        }
        .nav__list {
          display: flex;
          flex-direction: column;
          text-align: center;
        }
        .nav__list-item {
          margin-right: 0;
          margin-bottom: 2rem;
        }
        .nav__list-link {
          font-size: 18px;
        }
        .btn__contact {
          width: 50%;
          margin: 0 auto;
        }
      }
    }
}
.slip__nav {
  width: 100%;
  visibility: hidden;
  transform: translateY(-20px);
  transition: transform 1s ease-in-out;
  position: absolute;

  @media (min-width: 992px) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    visibility: visible;
    position: relative;
    transform: translateY(0);
    transition: none;
  }
}
.nav__list {
    list-style-type: none;
    margin: 0;
    padding: 0;

    @media (min-width:1200px) {
        margin-right: auto;
    }
}
.nav__list-item {
    display: inline;
    margin-right: 40px;
}
.nav__list-link {
    text-decoration: none;
    color: var(--primary);
    font-family: 'AvenirHeavy', sans-serif;
}

.btn__contact {
    background-color: var(--primary);
    color: white;
    font-size: inherit;
    padding: 9px 28px;
    border-radius: 22.5px;
}




// Section Heading`
.heading,
.subheading {
    color: #2178FF;
    font-family: 'AvenirBlack', sans-serif;
    margin-top: 0;
}
.heading {
    font-size: 30px;

    @media (min-width: 992px) {
      font-size: 55px;
    }
}
.subheading {
    font-size: 25px;

    @media (min-width: 992px) {
      font-size: 45px;
    }
}


// News
.news__section {
  padding: 2rem 0;
  @media (min-width: 992px) {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
.news__date {
  font-family: 'AvenirBook', sans-serif;
}
.news__title {
  text-align: center;
  margin-bottom: 2rem;

  @media (min-width: 992px) {
    width: 780px;
    text-align: left;
    margin-bottom: 60px;
  }

  .heading {
      margin-bottom: 20px;
  }
}
.swiper__desc {
  font-size: 1rem;
}

.swiper-container {
  width: 100%;
  padding-bottom: 50px;
}

.swiper-wrapper {
  align-items: flex-start;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  height: auto;

  @media (min-width: 992px) {
    height: 100%;
  }
}

.swiper__image {
  font-size: 0;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;

  img {
      border-radius: inherit;
      width: 100%;
      height: 200px;
      object-fit: cover;
  }
}

.swiper__desc {
  position: relative;
  top: -11px;
  background-color: white;
  border-radius: 14px;
  text-align: left;
  padding: 1.2rem 1rem;
}

.swiper-slide-active {
  .swiper__desc {
      box-shadow: 0 0 25px 0 rgba(33, 120, 255, 0.13);
  }
  .swiper__title {
      color: var(--primary);
  }
}

.swiper-pagination-fraction, .swiper-pagination-custom, .swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 0;
}
.swiper-pagination-bullet {
  width: 15px;
  height: 15px;
}





// Social
.social__content {
  text-align: center;
  padding: 82px 0;
}
.social {
  display: flex;
  justify-content: center;
}
.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 55px;
  height: 55px;
  background-color: #E1EFFF;
  border-radius: 10px;
  margin-right: 20px;
}





// App
.app__section {
  background-color: var(--primary);
  padding: 70px 0;
}
.app__content {
  text-align: center;
  color: white;
  width: 100%;
  line-height: 25px;

  @media (min-width: 992px) {
    width: 685px;
    margin: 0 auto;
  }

  p {
    margin-bottom: 35px;
  }

  .group__btn {
    display: flex;
    justify-content: center;
  }
}


// Footer
.footer__content {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  font-family: 'AvenirLight', sans-serif;
  font-size: 16px;
  padding: 1rem 0;
  text-align: center;

  @media (min-width: 992px) {
    flex-direction: row;
    text-align: left;
  }

  nav {
    margin-bottom: 1rem;
    @media (min-width: 992px) {
      margin-bottom: 0;
    }
  }

  a {
    color: inherit;
    text-decoration: none;
    
    &:not(:last-of-type) {
      margin-right: 3rem;
    }

    &:hover {
      color: var(--primary);
    }
  }
}
