.heading {
    margin-bottom: 30px;
}
.subheading {
    font-size: 35px;
}

// Hero
.slip__hero {
    position: relative;
    background-color: white;
    overflow: hidden;
    height: calc(100vh - var(--header-mobile-height));

    @media only screen and (min-width: 1200px) {
        height: calc(100vh - var(--header-height));
    }
}
.hero__content {
    position: relative;
    color: var(--primary);
    margin: 20% auto 0;
    z-index: 1;

    @media (min-width: 992px) {
        margin-top: 40px;
    }

    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}
.hero__desc {
    text-align: center;

    @media (min-width: 992px) {
        width: 650px;
        text-align: left;
    }

    p {
        margin: 0;
    }
}
.hero__heading {
    color: inherit;
    line-height: normal;
    width: 250px;
    margin: 0 auto 1rem;

    @media (min-width: 992px) {
        width: auto;
        line-height: 75px;
    }
}

.hero__image {
    display: none;

    @media (min-width: 992px) {
        display: block;
    }
    img {
        width: 80%;
        height: auto;
        object-fit: contain;

        @media (min-width: 992px) {
            width: 362px;
        }
    }
}

.hero__bg {
    position: absolute;
    left: 0;
    bottom: -48px;
    width: 100%;
    height: 55%;
    background: url('../images/people.svg') repeat-x center;
    background-size: cover;
    margin-top: 2%;
    z-index: 0;
}

  
// Features
.slip__features {
    padding: 2rem 0;

    @media (min-width: 992px) {
        flex-direction: row;
        text-align: left;
        padding: 137px 0 100px;
    }
  
}
.features__title {
    text-align: center;
    margin: 0 auto 116px;

    @media (min-width: 992px) {
        width: 775px;
    }

    p {
        margin: 0;
    }


}
.features__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    text-align: center;

    @media (min-width: 992px) {
        flex-direction: row;
        text-align: left;
    }
  
    &:not(:last-of-type) {
        margin-bottom: 3rem;

        @media (min-width: 992px) {
            margin-bottom: 150px;
        }
    }
}
.features__desc {
    @media (min-width: 992px) {
        width: 618px;
    }

    .subheading {
        margin-bottom: 20px;
    }

    p {
        line-height: 25px;
    }
}
.features__image {
    margin-bottom: 3rem;

    @media (min-width: 992px) {
        margin-bottom: 0;
    }

    img {
        width: 80%;
        object-fit: contain;

        @media (min-width: 992px) {
            width: 418px;
            height: 401px;
        }
    }
}